<template>
    <div class="container py-5">
      <form @submit.stop.prevent="handleSubmit">
        <div class="form-group">
          <label for="name">Name</label>
          <input v-model="name" id="name" type="text" class="form-control" name="name"  placeholder="Enter Name" required>
        </div>
        <div class="form-group">
          <label for="image">Image</label>
          <img v-if="image" :src="image" class="d-block img-thumbnail mb-3" width="200" height="200" />
          <input type="file" class="form-control-file" id="image" accept="image/*" name="image"
            @change="handleFileChange">
        </div>
        <button type="submit" class="btn btn-primary mr-5" :disabled="isProcessing">{{ isProcessing ? '資料更新中...' : 'Submit' }}</button>
        <button type="button" class="btn btn-secondary" @click="$router.back()">回上一頁</button>
      </form>
    </div>
</template>
<script>

import {mapState} from 'vuex'
import { Toast } from '../utils/helpers'
import usersAPI from '../apis/users'

export default {
  name: 'EditProfile',
  data() {
    return {
      id: 0,
      name: '',
      image: '',
      email: '',
      isProcessing: false
    }
  },
  computed: {
    ...mapState(['currentUser'])
  },
  watch:{
    currentUser(user){
      if(user.id === -1) return
      const {id} = this.$route.params
      this.setUser(id)
    }
  },
  created(){
    if (this.currentUser.id === -1) return
    const { id } = this.$route.params
    this.setUser(id)
  },
  beforeRouteUpdate(to, from, next){
    if (this.currentUser.id === -1) return
    const { id } = to.params
    this.setUser(id)
    next()
  },
  methods: {
    setUser(userId){
      const {id, image, name, email} = this.currentUser

      if(id.toString() !== userId.toString()){
        this.$router.push({name: 'not-found'})
        return 
      }
      this.id = id
      this.name = name
      this.email = email
      this.image = image
    },
    handleFileChange(e) {
      const { files } = e.target
      if (files.length === 0) {
        this.profile.image = ''
        return
      } else {
        const imageURL = window.URL.createObjectURL(files[0])
        this.image = imageURL
      }
    },
    async handleSubmit(e){
      try{
        if(!this.name){
          Toast.fire({
            icon: 'warning',
            title: '姓名尚未填寫'
          })
        }

        const form = e.target
        const formData = new FormData(form)

        this.isProcessing = true
        const {data} = await usersAPI.update({
          userId: this.id,
          formData
        })

        if(data.status !== 'success'){
          throw new Error(data.message)
        }

        this.$router.push({name: 'user', params: {id: this.id}})
      }catch(err){
        this.isProcessing = false
        console.log(err)
        Toast.fire({
          icon: 'error',
          title: '無法更新資料，請稍後再試'
        })
      }
    }
  }
}
</script>

